body {
  font-family: Helvetica, Arial, sans-serif;
}
.titulo {
  align-content: center;
  
}
.dropzone-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.dropzone {
  overflow: hidden;
  margin: 0.5em;
  padding: 1em;
  color: #666;
  text-align: center;
  background: #ccc;
  line-height: 4em;
  border: 4px dashed transparent;
  transition:
    background 0.15s linear,
    border-color 0.15s linear;
}

.dropzone.-drop-possible {
  border-color: #666;
}

.dropzone.-drop-over {
  background: #666;
  color: #fff;
}

.draggable {
  position: relative;
  z-index: 10;
  width: 200px;
  margin: 0.25em;
  padding: 1em 2em;
  background-color: #29e;
  color: #fff;
  text-align: center;

  -ms-touch-action: none;
  touch-action: none;
}

.draggable.-drop-possible {
  background-color: #53cf53;
}
